import React, { useState } from "react";
import { Button, Table, Modal, Form, InputGroup, FormControl } from "react-bootstrap";
import { FaEdit, FaTrash, FaCheck, FaTimes, FaSearch } from "react-icons/fa";
import Layout from "@/components/Layout"; // Layout Component
// import PaginationComponent from "@/components/PaginationComponent"; // Custom Pagination Component
import styles from "../styles/payment.module.css"; // Import custom styles

const PaymentManagementPage = () => {
  const [payments, setPayments] = useState([
    { id: 1, orderId: "ORD001", transactionId: "TXN001", amount: "100.00", status: "Pending", paymentMethod: "Credit Card", paymentDate: "2024-01-01" },
    { id: 2, orderId: "ORD002", transactionId: "TXN002", amount: "250.00", status: "Completed", paymentMethod: "PayPal", paymentDate: "2024-01-02" },
    { id: 3, orderId: "ORD003", transactionId: "TXN003", amount: "75.50", status: "Failed", paymentMethod: "Debit Card", paymentDate: "2024-01-03" },
    { id: 4, orderId: "ORD004", transactionId: "TXN004", amount: "200.00", status: "Pending", paymentMethod: "Credit Card", paymentDate: "2024-01-04" },
    // Add more static data for pagination demonstration
  ]);
  const [searchQuery, setSearchQuery] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [modalType, setModalType] = useState("");
  const [formData, setFormData] = useState({});
  const [processing, setProcessing] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(5);

  const totalPages = Math.ceil(payments.length / itemsPerPage);

  const paginatedPayments = payments.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const handleSearch = () => {
    const filteredPayments = payments.filter(payment => 
      payment.orderId.toLowerCase().includes(searchQuery.toLowerCase()) || 
      payment.transactionId.toLowerCase().includes(searchQuery.toLowerCase())
    );
    setPayments(filteredPayments);
  };

  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleAction = async (actionType, paymentId) => {
    setProcessing(true);
    setTimeout(() => {
      alert(`${actionType} action performed for Payment ID: ${paymentId}`);
      setProcessing(false);
      setShowModal(false);
    }, 1000);
  };

  return (
    <Layout>
      <div className="container mt-5">
        <h1 className={styles.pageTitle}>Payment Management</h1>

        {/* Search Bar */}
        <InputGroup className="mb-3">
          <FormControl
            placeholder="Search by Order ID or Transaction ID"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
          <InputGroup.Append>
            <Button variant="outline-secondary" onClick={handleSearch}>
              <FaSearch />
            </Button>
          </InputGroup.Append>
        </InputGroup>

        <Table striped bordered hover>
          <thead>
            <tr>
              <th>Payment ID</th>
              <th>Order ID</th>
              <th>Transaction ID</th>
              <th>Amount</th>
              <th>Status</th>
              <th>Payment Method</th>
              <th>Payment Date</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {paginatedPayments.length === 0 ? (
              <tr>
                <td colSpan="8" className="text-center">
                  No data available
                </td>
              </tr>
            ) : (
              paginatedPayments.map((payment) => (
                <tr key={payment.id}>
                  <td>{payment.id}</td>
                  <td>{payment.orderId}</td>
                  <td>{payment.transactionId}</td>
                  <td>{payment.amount}</td>
                  <td>{payment.status}</td>
                  <td>{payment.paymentMethod}</td>
                  <td>{payment.paymentDate}</td>
                  <td>
                    <Button
                      variant="secondary"
                      size="sm"
                      onClick={() => {
                        setModalType("update");
                        setFormData({
                          paymentId: payment.id,
                          status: payment.status,
                        });
                        setShowModal(true);
                      }}
                    >
                      <FaEdit />
                    </Button>{" "}
                    <Button
                      variant="danger"
                      size="sm"
                      onClick={() => handleAction("cancel", payment.id)}
                    >
                      <FaTimes />
                    </Button>{" "}
                    <Button
                      variant="success"
                      size="sm"
                      onClick={() => handleAction("complete", payment.id)}
                    >
                      <FaCheck />
                    </Button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </Table>

        {/* Pagination Component */}
        <PaginationComponent
          totalItems={payments.length}
          itemsPerPage={itemsPerPage}
          currentPage={currentPage}
          onPageChange={handlePageChange}
        />

        {/* Modal for Updating Payment Status */}
        <Modal
          show={showModal}
          onHide={() => setShowModal(false)}
          dialogClassName="modal-dialog-centered"
          className={styles.customModal}
        >
          <Modal.Header closeButton>
            <Modal.Title className={styles.modalTitle}>
              {modalType === "update" && "Update Payment Status"}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form>
              <Form.Group>
                <Form.Label>Status</Form.Label>
                <Form.Control
                  as="select"
                  name="status"
                  value={formData.status || ""}
                  onChange={handleFormChange}
                  className={styles.modalInput}
                >
                  <option value="Pending">Pending</option>
                  <option value="Completed">Completed</option>
                  <option value="Failed">Failed</option>
                </Form.Control>
              </Form.Group>
              <Button
                type="button"
                onClick={() => handleAction(modalType, formData.paymentId)}
                disabled={processing}
                className={styles.modalButtonPrimary}
              >
                {processing ? "Processing..." : "Save Changes"}
              </Button>
            </Form>
          </Modal.Body>
        </Modal>
      </div>
    </Layout>
  );
};

export default PaymentManagementPage;
